@import url(https://netdna.bootstrapcdn.com/font-awesome/4.1.0/css/font-awesome.min.css);
body, 
button, 
input, 
option, 
select, 
textarea 
{
    font-size: 13px;
    font-family: "Open Sans", sans-serif;
    color: #000;
    text-align: left;
    -webkit-text-size-adjust: 100%;
}

ol, ul {
    list-style: none;
    padding: 0px;
    margin: 0px;
}

li {
    margin-bottom: 1em;
}
.content-wrapper {
    width: 80%;
    padding: 10px;
    border: 1px solid #ddd;
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
    background-color: #fff;
    box-sizing: border-box;
}

.founder-component {
    display: flex;
    flex-direction: column;
    padding: 10px;
}

.item-wrapper {
    padding-left: 20px;
    display: flex;
    box-sizing: border-box;
    vertical-align: top;
    margin-top: 20px;
    margin-bottom: 20px;
    align-items: center;
}

.info-block {
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex: 1 1;
}

.company-logo {
    padding: 10px;
}

.company-info {
    display: flex;
    flex-direction: column;
    cursor: pointer;
}

.company-info a {
    text-decoration: none;
    color: black;
    display: inherit;
    flex-direction: inherit;
}

.company-info .name {
    font-weight: 600;
    font-size: 1.3em;
    margin-top: 5px;
    margin-bottom: 10px;
}

.book-button {
    box-sizing: border-box;
    border: 2px solid #ffc905;
    background-color: white;
    border-radius: 0.6em;
    color: black;
    cursor: pointer;
    display: flex;
    align-self: center;
    font-size: 1rem;
    font-weight: 400;
    line-height: 1;
    margin: 20px;
    padding: 1.2em 2.8em;
    text-decoration: none;
    text-align: center;
    font-family: 'Montserrat', sans-serif;
    font-weight: bold; /* Safari */
    transition-duration: 0.4s;
}
  
.book-button:hover {
background-color: #ffc905; 
color: white;
}

img {
    width: 100px;
    height: 100px;
    border-radius: 50%;
    border: 2px solid #ffc905;
    cursor: pointer;
}

.rate-list {
    display: flex;
}

.rate-list img {
    cursor: pointer;
    border: none;
    width: 20px;
    height: 20px;
}

i {
    font-family: fontawesome !important;
    font-style: normal;
    font-style: initial;
}

body{
    background: #f2f2f2;
    font-family: 'Open Sans', sans-serif;
}

.search {
    display: flex;
    width: 100%;
}

.searchTerm {
    width: 100%;
    border: 3px solid #00B4CC;
    padding: 5px;
    height: 20px;
    border-top-left-radius: 5px;
    border-bottom-left-radius: 5px;
    outline: none;
    color: #9DBFAF;
}

.searchTerm:focus{
    color: #00B4CC;
}

.searchButton {
    border: 1px solid #00B4CC;
    background: #00B4CC;
    text-align: center;
    color: #fff;
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
    cursor: pointer;
    font-size: 20px;
}

/*Resize the wrap to see the search bar change!*/
.search-container{
    text-align: right;
    margin: 0px 0px 20px auto;
    width: 30%;
}

.fa-search:before {
    content: "\F002";
}

.filter-wrapper {
    width: 20%;
    height: auto;
    padding: 10px;
    border-left: 1px solid #ddd;
    border-top: 1px solid #ddd;
    border-bottom: 1px solid #ddd;
    border-top-left-radius: 5px;
    border-bottom-left-radius: 5px;
    background-color: #fff;
}

.service-container {
    display: flex;
    flex-direction: row;
    justify-content: center;
}

.cost-container {
    display: flex;
    width: 80%;
}

.cost-container form {
    display: inherit;
    width: 100%;
}

.cost-container div {
    display: flex;
    width: 50%;
    flex-direction: column;
}

.cost-container input {
    color: #9DBFAF;
    font-size: 13px;
    margin-right: -2px;
    padding: 5px;
    border-radius: 3px;
    outline: none;
    border: 2px solid #00B4CC;
}

.filter-block {
    margin-bottom: 1em;
    display: flex;
    flex-direction: column;
}

.filter-block h4, p {
    margin: .2em;
    padding: 10px 0;
    color: #9a9a9a;
    text-transform: uppercase;
    font-weight: 700;
    font-size: 0.8rem;
}

.filter-block span {
    margin-bottom: .2em;
    padding: 0px;
    color: #9a9a9a;
    font-weight: 700;
    font-size: 0.8rem;
}

input[type='number'] {
    -moz-appearance:textfield;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
    -webkit-appearance: none;
}

::-webkit-input-placeholder { 
    color: #ccc;
    opacity: 1; 
}

::-ms-input-placeholder { 
    color: #ccc;
    opacity: 1; 
}

::placeholder { 
    color: #ccc;
    opacity: 1; 
}

.container {
    display: block;
    position: relative;
    padding-left: 30px;
    margin-bottom: 15px;
    cursor: pointer;
    font-size: 1.0em;
    font-weight: 400;
    color: #9a9a9a;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
  }
  
  .container input {
    position: absolute;
    opacity: 0;
    cursor: pointer;
    height: 0;
    width: 0;
  }
  
  .checkmark {
    position: absolute;
    top: 0;
    left: 0;
    height: 20px;
    width: 20px;
    border-radius: 3px;
    background-color: #00B4CC;
  }
  
  .container:hover input ~ .checkmark {
    background-color: #ffc905;
  }

  .container input:checked ~ .checkmark {
    background-color: #ffc905;
  }
  
  .checkmark:after {
    content: "";
    position: absolute;
    display: none;
  }
  
  .container input:checked ~ .checkmark:after {
    display: block;
  }
  
  .container .checkmark:after {
    left: 7px;
    top: 2px;
    width: 4px;
    height: 9px;
    border: solid white;
    border-width: 0 3px 3px 0;
    -webkit-transform: rotate(45deg);
    transform: rotate(45deg);
  }
